import React from "react"
import { graphql } from "gatsby"
import PostLink from "../components/post-link"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { CardDeck, Card, Container } from "react-bootstrap"

const BlogPage = ({
  data: {
    allMarkdownRemark: { edges },
  },
}) => {
  const Posts = edges
    .filter(edge => !!edge.node.frontmatter.date) // You can filter your posts based on some criteria
    .map(edge => <PostLink key={edge.node.id} post={edge.node} />)
  return (
    <Layout>
      <SEO title="Blog" />
      <Container
        className={"d-flex align-items-center justify-content-center px-0 my-4"}
        style={{ minHeight: "calc(100vh - 154px)" }}
      >
        <Card className={"glass"}>
          <Card.Body>
            {Posts.lenght > 0 ? (
              <CardDeck> {Posts} </CardDeck>
            ) : (
              <Card.Title>
                <center>
                  Seems to be there is no post yet :( <br /> Come back later to
                  see an update on what im currently working on
                </center>
              </Card.Title>
            )}
          </Card.Body>
        </Card>
      </Container>
    </Layout>
  )
}
export default BlogPage

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            slug
            title
            description
            featuredImage {
              childImageSharp {
                fluid(maxWidth: 300) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    }
  }
`
